.popup-box {
    position: fixed;
    /* background: #00000050; */
    /* width: 30%;
    height: 30vh; */
    top: 5px;
    left: 33%;
   
  }
   
  .box {
    position: relative;
    width: 180px;
    margin: 0 auto;
    height: 90px;
    max-height: 70vh;
    margin-top: calc(100vh - 85vh - 20px);
    background: rgb(73, 60, 60);
    border-radius: 4px;
    padding: 20px;
    border: 1px solid #999;
  
  }
   
  .close-icon {
    content: 'x';
    cursor: pointer;
    position: fixed;
    right: calc(15% - 30px);
    top: calc(100vh - 85vh - 33px);
    background: #ededed;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 20px;
  }

